body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.header-top {
  color: #fff;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header-logo {
  width: 50px;
  height: auto;
}

.searchbox {
  background-color: #fff;
  padding: 10px;
}

.container {
  height: calc(100vh - 100px);
  display: flex;
}

.sidebar {
  box-sizing: border-box;
  background-color: #f4f4f4;
  flex-shrink: 0;
  width: 20%;
  padding: 20px;
}

.main-content {
  box-sizing: border-box;
  background-color: #90ee90;
  flex: 1;
  max-width: 80%;
  padding: 20px;
  overflow: auto;
}

#hits {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.ais-Hits-list {
  display: contents;
}

.ais-Hits-item {
  box-sizing: border-box;
  background-color: #ff0;
  border: 2px solid #00f;
  width: 100%;
}

.hit {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #ddd;
  width: 100%;
  height: 100%;
  padding: 10px;
}

.hit-image {
  width: 100%;
  height: auto;
  display: flex;
}

#pagination {
  margin-top: 20px;
}
